* {
    font-family: 'IBM Plex Sans Arabic', sans-serif;
}

body {
    background-color: #E7E7E7;

}

h1 {
    margin: 0;
    padding-top:100px;
    height: 200px;
    background-color: #fff;
    /* color: #fff; */
    text-align: center;
    font-size: 80px;
}
.container{
    margin: auto;
    width: 60%;
    display: block;
}
.addTodos {
    text-align: center;
}

.todo-input {
    margin: 20px;
    padding: 0px 20px;
    width: 60%;
    height: 27px;
    background-color: rgba(0, 0, 0, 0.795);
    color: #fff;
    border: none;
    border-radius: 15px;
}

.add-btn {
    width: 15%;
    height: 27px;
    color: #fff;
    background-color: #F53164;
    border: 1px solid #F53164;
    border-radius: 15px;
}
ul{
    display: flex;
    flex-direction: column-reverse;
    margin: 0px ;
    padding: 0px;
}
ul li {
    list-style: none;
    width: 100%;
    height: 35px;
    margin-left: 64px;
    display: flex;
}

textarea {
    align-items: center;
    width: 65%;
    height: 27px;
    margin: 0px 30px 0px 10px;

    padding: 0px 20px;
    box-sizing: border-box;
    resize: none;
    border-radius: 15px;
    float: left;
}
li div{
    align-items: center;
 float: right;
 width: 15%;
}
li button {
    margin: 3px;
    background-color: black;
    border-radius: 50%;
    border: none;
}

.icon {
    color: #fff;
    margin-top: 4px;
}